import { Field, ObjectType } from "@nestjs/graphql";
import { Column, Entity, JoinTable, ManyToMany, ManyToOne, OneToMany, Relation } from "typeorm";
import { BaseEntityColumn } from "../base.entity";
import { Brand } from "./brand.entity";
import { PaymentGatewayPlan } from "./payment-gateway-plan.entity";
import { PaymentGateway } from "./payment-gateway.entity";
import { Paypal } from "./paypal.entity";

@ObjectType()
@Entity()
export class PaymentGatewayKey extends BaseEntityColumn {

  @Field(() => String)
  @Column()
  clientId: string;

  @Field(() => String)
  @Column()
  clientSecret: string;

  @Field(() => PaymentGateway)
  @ManyToOne(() => PaymentGateway, (paymentGateway) => paymentGateway.paymentGatewayKeys)
  paymentGateway: PaymentGateway;

  @Field(() => Brand)
  @ManyToOne(() => Brand, (brand) => brand.paymentGatewayKeys, { orphanedRowAction: "soft-delete" })
  brand: Relation<Brand>;

  @Field(() => [PaymentGatewayPlan])
  @ManyToMany(() => PaymentGatewayPlan, (paymentGatewayPlan) => paymentGatewayPlan.paymentGatewayKeys, { cascade: true })
  @JoinTable({
    name: 'payment_gateway_key_payment_gateway_plan'
  })
  paymentGatewayPlans: PaymentGatewayPlan[];

  @Field(() => [Paypal])
  @OneToMany(() => Paypal, (paypal) => paypal.paymentGatewayKey)
  paypals: Paypal[];
  
}
